.accordion__list {
  list-style-type: none;
  padding: 0;
  margin: 0;

  &-site {
    display: flex;

    @media (max-width: $tablet-width) {
      flex-direction: column;
      width: 300px;
    }
  }

  @media (max-width: $mobile-width) {
    display: none;
  }

  &--opened {
    @media (max-width: $mobile-width) {
      display: flex;
      flex-direction: column;
    }
  }
}

.accordion__content {
  margin-bottom: 7px;

  @media (max-width: $tablet-width) {
    margin-bottom: 5px;
  }
}

.accordion__link {
  font-family: $pt-mono;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: $color-default-white;
  opacity: 0.7;

  &:hover,
  &:focus {
    opacity: 0.9;
  }

  &:active {
    opacity: 0.5;
  }
}

.accordion__wrapper {
  @media (max-width: $mobile-width) {
    position: relative;
    padding: 20px 0;
    padding-bottom: 15px;
  }
}

.accordion__toggle {
  display: none;

  @media (max-width: $mobile-width) {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;

    &::after,
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      width: 15px;
      height: 1px;
      background-color: $color-default-white;
    }

    &::before {
      transform: rotate(-270deg);
    }
  }

  &--opened {
    @media (max-width: $mobile-width) {
      &::before {
        transform: rotate(540deg);
      }
    }
  }
}

.accordion {
  margin-right: -10px;
  padding-top: 5px;

  @media (max-width: $tablet-width) {
    margin-right: -36px;
  }

  h3 {
    font-family: $roboto-condensed;
    font-weight: 400;
    font-size: 15px;
    line-height: 16px;
    letter-spacing: 0.05em;
    margin: 0;
    text-transform: uppercase;
    margin-bottom: 20px;

    @media (max-width: $mobile-width) {
      margin-bottom: 0;
    }
  }

  @media (max-width: $mobile-width) {
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    width: 100%;
    height: 100%;
    padding-bottom: 10px;
  }
}

.accordion__item {
  &:not(:first-child) {
    max-width: 355px;
  }
}

.accordion__content-wrapper {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 50%;

  @media (max-width: $tablet-width) {
    width: 75%;
  }

  &:first-child {
    margin-right: 30px;
  }

  &:last-child{
    margin-left: 5px;
  }
}
